* {
  box-sizing: border-box; }

html {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 15px; }

body {
  min-height: 100vh; }

body,
ul,
ol,
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0; }

a {
  text-decoration: none; }

ul,
ol {
  list-style: none; }

.page-content {
  max-width: 960px;
  margin: 0 auto;
  padding: 0 8px; }

.product {
  width: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
  color: #33333a;
  font-size: 16px; }
  .product > * {
    margin-bottom: 12px; }
  .product__image {
    width: 100%;
    display: block; }
  .product__label {
    position: absolute;
    top: 20px;
    left: 20px;
    background: #fe6007; }
  .product__price {
    font-size: 14px; }
  .product__description {
    display: flex;
    flex-direction: column;
    position: relative; }
